.navbar-light {
  background-color: transparent;
  /* box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2); */
}

.outer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  margin-top: 10vh;
}

.inner {
  width: 35vw;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.outer .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.outer h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}

.mainContainer {
  min-height: 100vh;
  background: center url("../assets/background.jpg");
}
